/**
 * fetchfetchreject
 * 400,500rejectresolve
 *  
 *    status1001   504   http
 *  
 *    code 0
 *          
 * @param {Object} options
 *  credentials
 *    credentials: 'include' )
 *    credentials: 'same-origin' URL
 *    credentials: 'omit' 
 *  data
 */
function request(options) {
  const config = {
    method: 'GET',
    headers: {
      'Cache-Control': 'max-age=0',
      'Pragma': 'no-cache',
      'Accept': 'application/json',
      'Content-Type': 'application/x-www-form-urlencoded',
      'x-requested-with': 'fetch',
      body: null,
      credentials: 'include'
    }
  }

  return new Promise((resolve, reject) => {
    let fetchFinished = false
    abortablePromise(fetch(options.url, config), 1000)
      .then(res => {
        fetchFinished = true
        if (res.status !== 200) {
          throw res
        }
        return res.json()
      })
      .then(data => {
        resolve(data)
      }).catch(err => {
        if (err.timeout) {
          fetchFinished = true
        }
        // 
        if (!fetchFinished) {
          err.status = 1001
          err.statusText = 'Failed to fetch'
        }
        reject(err)
      })
  })
}

function abortablePromise(fetchPromise, timeout) {
  let aboutFn = null
  const abortPromise = new Promise((resolve, reject) => {
    aboutFn = () => {
      reject({
        status: 504,
        statusText: 'abort promise',
        timeout
      })
    }
  })

  const _abortablePromise = Promise.race([
    fetchPromise, abortPromise
  ])

  setTimeout(() => {
    aboutFn()
  }, timeout)
  return _abortablePromise
}

request({
  url: '/weather_mini?city=%E5%8C%97%E4%BA%AC'
}).then(res => {
  console.log(res)
}).catch(err => {
  console.log('err', err)
})
