/**
 * @param path
 */
export function isExternalLink(path: string) {
  return /^(https?:|mailto:|tel:)/.test(path);
}

export function checkAge(rule: any, value: number, callback: any) {
  console.log(value)
  if (!value) {
    callback('');
  } else if (!Number.isInteger(value)) {
    callback('');
  } else if (value < 18) {
    callback('18');
  } else {
    callback();
  }
}
